import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Button from "../../components/button";
import { Stack } from "@mui/material";
import SmartUdyog from '../../assets/png/dark-logo.png'

const SelectLogin = () => {
  const navigate = useNavigate()
  const [users, setUsers] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);

  return (
    <div className="mx-4 my-4">
      <Stack sx={{width:'100%',alignItems:'flex-end'}}>
      <img src={SmartUdyog} width='80px' height='43px' style={{objectFit:'contain'}}/>
      </Stack>
      <Stack sx={{flexDirection:'row',justifyContent:'center',alignContent:'center',gap:2}}>
      <Button onClick={() => navigate('/login')}>Admin</Button>
      <Button onClick={() => navigate('/oplogin')}>Operator</Button>
      </Stack>
    </div>
  );
};

export default SelectLogin;
